___CSS_LOADER_EXPORT___.push([module.id, "\n#balekMainScreenshot{\n\n  max-width: 80%;\n}\n\n\n", "",{"version":3,"sources":["webpack://./src/components/balek/Info.vue"],"names":[],"mappings":";AA6CA;;EAEE,cAAc;AAChB","sourcesContent":["<template>\n  <div class=\"InfoDiv\">\n    <div class=\"MainInfoTitleDiv\">\n      <img class=\"MainInfoTitleLogoImg\" :src=\"balekLogo\"  alt=\"Balek Logo\"/>\n      Balek\n    </div>\n\n    <div class=\"MainInfoSummary\">\n      Full Stack Javascript Framework\n    </div>\n\n    <br/>\n    <a href=\"/balek/resources/screenshot.jpg\" target=\"_blank\"><img id=\"balekMainScreenshot\" :src=\"screenShotThumb\"  alt=\"Balek Screenshot\"/></a>\n    <div class=\"MainInfoDescription\" style=\"margin-bottom:40px;\">\n      <div class=\"MainInfoDiv\">\n        Balek is a full-stack JavaScript framework designed for developing, building, and deploying web applications that are remotely accessible and persist separate from their interfaces. With Balek, you can build modules using provided APIs and combine them to create powerful web applications.\n        <br/>\n        <br/>\n        The framework includes login and user management modules to streamline the development process. Balek also provides a modular architecture that makes it easy to add custom functionality as your project evolves.\n        <br/>\n        <br/>\n        Click our screenshot to see a larger version, and watch our use cases video to see how Balek in action.\n      </div>\n    </div>\n\n\n  </div>\n</template>\n<script>\nimport balekLogo from './resources/Balek.svg'\n\nimport screenShotThumb from './resources/screenshotThumb.png'\n\nexport default {\n  name: \"BalekInfo\",\n  data: function () {\n    return {\n      balekLogo, screenShotThumb\n    }\n  }\n};\n</script>\n<style>\n\n\n#balekMainScreenshot{\n\n  max-width: 80%;\n}\n\n\n</style>"],"sourceRoot":""}]);
