___CSS_LOADER_EXPORT___.push([module.id, "\n.mainSection{\n  position: relative;\n  display: inline-block;\n  border-top: solid black 4px;\n\n  width: 100%;\n  background-color: rgba(0.5,0.5,0.5,0.0);\n}\n.balekMainDiv{\n  position:absolute;\n  width: 100%;\n  font-size: 36pt;\n  text-align: center;\n  color:  #205377;\n}\n\n", "",{"version":3,"sources":["webpack://./src/components/balek/Main.vue"],"names":[],"mappings":";AAoDA;EACE,kBAAkB;EAClB,qBAAqB;EACrB,2BAA2B;;EAE3B,WAAW;EACX,uCAAuC;AACzC;AACA;EACE,iBAAiB;EACjB,WAAW;EACX,eAAe;EACf,kBAAkB;EAClB,eAAe;AACjB","sourcesContent":["<template>\n  <div class=\"balekMainDiv\">\n\n    <BalekInfo style=\"border: none; background: none;\" />\n\n    <div class=\"MainInfoDiv\">\n\n    <div class=\"MainInfoTitleDiv\" style=\"text-align: left; margin-top: 30px; border-bottom: solid black 2px;\">\n      Use Cases:\n    </div>\n      <br/>\n      <br/>\n      Balek has been used to create a small variety of successful projects, including Diaplode, a multi-user remote workspace used for a variety of purposes, from staging projects to researching notes. Coopilot, a web app and iOS app used in a POS specialist position at a food coop, was also built using Balek. These projects have been deployed in a variety of environments including MacOS, Linux, OpenBSD, Windows, and Google Cloud.\n      <br/>\n      <br/>\n    <div class=\"MainYouTubeDiv\">\n      <iframe class=\"MainYoutubeVideoIframe\"  src=\"https://www.youtube.com/embed/n3bwKBiYDTU\" title=\"YouTube video player\" frameborder=\"0\" allow=\"accelerometer; autoplay; clipboard-write; encrypted-media; gyroscope; picture-in-picture\" allowfullscreen=\"\"></iframe>\n    </div>\n    <div class=\"MainInfoTitleDiv\" style=\"text-align: left; margin-top: 30px; border-bottom: solid black 2px;\">\n      Availability:\n    </div>\n    <div class=\"MainInfoDiv\">\n      Balek is in the process of being cleaned up and documented for public use. Once completed, a pre-alpha branch will be available <a target=\"_blank\" class=\"MainLink\" href=\"https://www.github.com/digivigil/balek\">on Digivigil's Github</a>\n\n      <br/>\n      <br/>\n      The Current prototype version can be found <a target=\"_blank\" class=\"MainLink\" href=\"https://www.github.com/ephedrandrox/balek\">here</a>\n    </div>\n</div>\n  </div>\n</template>\n<script>\nimport balekLogo from './resources/Balek.svg'\nimport digivigilLogo from '../digivigil/resources/digivigil.svg'\n\nimport BalekInfo from './Info.vue'\n\nexport default {\n  name: \"Balek\",\n  data: function () {\n    return {\n      balekLogo: balekLogo,\n      digivigilLogo: digivigilLogo\n    }\n  },\n  components: {\n    BalekInfo\n  }\n};\n</script>\n<style>\n\n.mainSection{\n  position: relative;\n  display: inline-block;\n  border-top: solid black 4px;\n\n  width: 100%;\n  background-color: rgba(0.5,0.5,0.5,0.0);\n}\n.balekMainDiv{\n  position:absolute;\n  width: 100%;\n  font-size: 36pt;\n  text-align: center;\n  color:  #205377;\n}\n\n</style>"],"sourceRoot":""}]);
